export default {
	data() {
		return {
			coupons:[],
			type:{},
			show: false
		}
	},
	onLoad() {
		uni.request({
			url:'http://localhost:8080/discount/selectAll',
			method: 'POST',
			data: {},
			success: res => {
				this.coupons=res.data;
				// 时间问题
				for (var i in this.coupons) {
					this.coupons[i].endtime = this.happenTimeFun(this.coupons[i].endtime);
				}
				this.couponsType();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		showPopup() {
		    this.show = true ;
		},
		
		onClose() {
			this.show = false ;
		},
		openchanpin() {
			uni.reLaunch({
			  	url: '/pages/producttype/chanpin/chanpin',
			  	success: res => {},
			  	fail: () => {},
			  	complete: () => {}
			});
		},
		openstores() {
			uni.reLaunch({
			  	url: '/pages/home/stores/stores',
			  	success: res => {},
			  	fail: () => {},
			  	complete: () => {}
			});
		},
		couponsType() {
			uni.request({
				url:'http://localhost:8080/discount/coupontypes',
				method: 'POST',
				data: {},
				success: res => {
					var data=res.data;
					for(var i in data){
						this.$set(this.type,data[i].couponTypeId,data[i].couponTypeName)
					}
					console.log(this.type)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		happenTimeFun(num){//时间戳数据处理
				let date = new Date(num);
				 //时间戳为10位需*1000，时间戳为13位的话不需乘1000
		        let y = date.getFullYear();
		        let MM = date.getMonth() + 1;
		        MM = MM < 10 ? ('0' + MM) : MM;//月补0
		        let d = date.getDate();
		        d = d < 10 ? ('0' + d) : d;//天补0
		        return y + '年' + MM + '月' + d + '日';
		}
	}
}
